@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
:root {
  --header-height: 470px;
  --main-body-height: 0px;
  --main-body-padding-top: 0px;
}

.rcw-header {
  background: linear-gradient(124.19deg, #2E408D 0%, #79C6E9 100%);
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);

  padding: 15px 19px 17px 19px !important;
  height: 470px;
  height: var(--header-height);
}

.rcw-conversation-container .avatar {
  border-radius: 0 !important;
  width: 50px !important;
  height: 50px !important;
}

.rcw-title {
  display: flex;
  padding: 0px !important;

  height: 100%;
}

.rcw-messages-container{
  height:0px !important ;
  height:var(--main-body-height) !important ;
  padding-top: 0px !important;
  padding-top: var(--main-body-padding-top) !important;
}

.rcw-messages-container::-webkit-scrollbar {
  background-color: transparent;
  width: 6px;
}
.rcw-messages-container::-webkit-scrollbar-track {
  border-radius: 6px;
  background: transparent;
}
.rcw-messages-container::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background: #cecece;
}

.rcw-message {
  margin: 20px !important;
}

.rcw-response,
.rcw-client {
  flex-direction: row !important;
  justify-content: space-between;
  width: 100%;
}

.rcw-client {
  color: #ffffff;
  margin-left: 0px;
}

.rcw-client .rcw-message-text {
  background-color: #79c6e9 !important;
}

.rcw-client .rcw-timestamp {
  align-self: center !important;
}

.rcw-response .rcw-message-text {
  background-color: #d8d9db !important;
}

.rcw-timestamp {
  align-self: center;

  color: #1d2629;

  font-weight: 500;
  font-size: 14px !important;
  line-height: 14px;

  margin-top: 0px !important;

  opacity: 0.5;
}

.rcw-message-text {
  padding: 10px 13px !important;
}

.rcw-message-text p {
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.282353px;
}

.rcw-sender {
  background-color: #ffffff !important;
  border-top: 0.5px solid #cecece;
}

.rcw-new-message {
  background-color: #ffffff !important;
  font-size: 16px;
  line-height: 19px;
}

input.rcw-new-message::-webkit-input-placeholder {
  color: #1d2629;

  font-size: 16px;
  line-height: 19px;

  opacity: 0.5;
}

input.rcw-new-message:-ms-input-placeholder {
  color: #1d2629;

  font-size: 16px;
  line-height: 19px;

  opacity: 0.5;
}

input.rcw-new-message::-ms-input-placeholder {
  color: #1d2629;

  font-size: 16px;
  line-height: 19px;

  opacity: 0.5;
}

input.rcw-new-message::placeholder {
  color: #1d2629;

  font-size: 16px;
  line-height: 19px;

  opacity: 0.5;
}

.rcw-send {
  background: #ffffff !important;
  outline: none;
}

.rcw-send img {
  content: url(/widget/static/media/sendIcon.ee41f8ff.svg);
}

.quick-button {
  border: 1px solid #619ccc !important;
  border-radius: 10px !important;

  color: #619ccc;
  font-weight: 300 !important;
  font-size: 16px;
  letter-spacing: 0.282353px;

  padding: 10px 20px !important;
}

.quick-button:active {
  background: #619ccc !important;
}

body {
  font-family: "Source Sans Pro", sans-serif;
}
.hello-page__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hello-page__title {
  font-size: 40px;
  line-height: 48px;

  text-align: start;
}

.hello-page__footer {
  margin-bottom: 35px;
}

.hello-page__woman {
  display: block;

  height: 80px;
  width: 80px;
}

.hello-page__primary-text {
  font-size: 30px;
  line-height: 36px;

  text-align: start;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}

.hello-page__primary-text--last {
  margin-bottom: 14px;
}

.hello-page__secondary-text {
  font-size: 16px;
  line-height: 21px;
  font-weight: 300;
  letter-spacing: 0.282353px;

  text-align: start;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}

.launcher__button {
  align-items: center;
  align-self: flex-end;

  background: linear-gradient(135deg, #2e408d 0%, #79c6e9 63.65%);

  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 50%;

  display: flex;
  justify-content: center;

  margin-top: 20px;

  outline: none;

  height: 60px;
  width: 60px;
}

.launcher__icon {
  margin-top: 5px;
}

.launcher__tooltip {
  background-color: #404040;

  border-radius: 7px 7px 0px 7px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  color: #fff;

  display: none;

  left: 28%;
  top: 20%;
  position: absolute;

  padding: 5px 20px;
  text-align: center;

  z-index: 1;
}

.launcher__tooltip::after {
  border-color: transparent transparent transparent #404040;
  border-style: solid;
  border-width: 10px;

  content: "";

  left: 100%;
  top: 100%;
  position: absolute;

  transform: rotate(45deg) scale(2, 0.5);
}

.launcher__button:hover .launcher__tooltip {
  display: block;
}

.header__text {
  text-align: start;
}

.header__bot {
  font-size: 22px;
  font-weight: 600;
  line-height: 26px;
}

.header__status {
  font-size: 0;
}

.header__circle {
  background-color: #95fac9;
  border-radius: 50%;

  display: inline-block;

  margin-right: 3px;

  height: 8px;
  width: 8px;
}

.header__online {
  font-size: 16px;
  font-weight: 300;
  line-height: 19px;

  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.4;
}

.messages__today-text {
  background-color: #efefef;
  border-radius: 10px;

  color: #1d2629;

  font-size: 16px;
  line-height: 19px;

  margin: 0 auto;

  text-align: center;

  opacity: 0.5;

  padding: 3px 10px;
}

